@import url(https://fonts.googleapis.com/css2?family=Paytone+One&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600&display=swap);
html {
	width: 100%;
	height: 100%;
}

body {
	width: 100%;
	height: 100%;
	margin: 0;
	font-family: 'Raleway', sans-serif;
	/* -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; */
	background-color: rgb(48, 46, 61);
}

root {
	width: 100%;
	height: 100%;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

a {
	color: turquoise;
	text-decoration: none;
}
a:hover {
	color: cyan;
}




header {
	position: fixed;
	top: 0;
	width: 100%;
	height: 10vh;
	display: flex;
	align-items: center;
	justify-content: space-around;
}

.logo {
	font-family: 'Paytone One';
	font-size: 42pt;
	color: tomato;
	letter-spacing: -6px;
	text-shadow: 0px -7px peachpuff;
}

header span {
	color: turquoise;
	text-shadow: 0px -7px darkcyan;
}

nav {
	top: 10vh;
	right: 3%;
	z-index: 200;
	float: right;
	position: fixed;
	width: 40%;
	border-radius: 5px;
	box-shadow: -3px 3px 6px inset #191b29;
}

ul {
	list-style: none;
	padding: 2px;
}

li {
	padding: 7%;
}

.login {
	height: 100vh;
	width: 100%;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
}

form {
	width: 100%;
}

.friends {
	height: 100%;
	width: 100%;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
}

.invite-friend form {
	width: 100%;
	height: 50px;
	display: grid;
	grid-template-columns: auto 5px auto 5px 40px;
	grid-template-rows: -webkit-min-content -webkit-min-content;
	grid-template-rows: min-content min-content;
	grid-gap: 2px;
	gap: 2px;
	place-items: center;
}

.invite-friend input[type='text'] {
	width: 100%;
}

#id-label {
	grid-column: 1;
	grid-row: 1;
}

#name-label {
	grid-column: 1;
	grid-row: 2;
}

#new-friend-id {
	grid-column: 3;
	grid-row: 1;
}

#new-friend-name {
	grid-column: 3;
	grid-row: 2;
}

#submit-new-friend {
	grid-column: 5;
	grid-row: 1;

	font-weight: 500;
	font-size: 8pt;
	height: 20px;
	width: 35px;
	border-radius: 35px;
	padding: 0;
	background-color: #008b8b;
	border: none;
	color: turquoise;
}

#delete-friend {
	grid-column: 5;
	grid-row: 2;

	font-weight: 1000;
	font-size: 10pt;
	height: 20px;
	width: 35px;
	border-radius: 35px;
	background-color: tomato;
	border: none;
	color: peachpuff;
}

.friends-list {
	height: 80%;
	overflow: auto;
}

.friends-list ul {
	overflow-y: hidden;
	overflow-x: auto;
}

.friends-list li {
	padding: 10px 5px;
}

.added-friends {
	margin: 0;
}

.games {
	height: 100%;
	width: 100%;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
}

.start-game div {
	height: 50px;
	margin-bottom: 5px;
}

.start-game form {
	width: 100%;
	height: 50px;
	display: grid;
	grid-template-columns: auto 5px 40px;
	grid-template-rows: -webkit-min-content -webkit-min-content;
	grid-template-rows: min-content min-content;
	grid-gap: 2px;
	gap: 2px;
	place-items: center;
}

#new-game-label {
	grid-column: 1;
	grid-row: 1;
	justify-self: right;
	padding-right: 5px;
}

.start-game select {
	width: 100%;
	grid-column: 1;
	grid-row: 2;
	justify-self: left;
}

#start-game-button {
	grid-column: 3;
	grid-row-start: 1;
	grid-row-end: span 2;

	font-weight: 500;
	font-size: 15pt;
	height: 35px;
	width: 35px;
	border-radius: 35px;
}

.created-games ul {
	overflow-x: hidden;
	overflow-y: auto;
}

.created-games div {
	padding: 0;
	margin: 0;
	width: 100%;
	cursor: pointer;
	display: grid;
	grid-template-columns: auto -webkit-min-content;
	grid-template-columns: auto min-content;
}

.selected-game div {
	background-color: peachpuff;
	border-radius: 25px;
}

.created-games li {
	padding: 3%;
	width: 70%;
}

.created-games li:active {
	color: tomato;
}

.created-games button {
	width: 20%;
}

.created-games button:hover {
	background-color: inherit;
}


.modal-overlay {
	position: fixed;
	z-index: 999;
	height: 100%;
	width: 100%;
	background-color: rgba(0, 56, 56, 0.568);
}

.open-game-modal {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	background-color: peachpuff;
	z-index: 1000;
	padding: 2%;
	border-radius: 15px;
	width: 80%;
	height: 78%;

	display: grid;
	grid-template-columns: 1fr 40px;
	grid-template-rows:
		[header-start] 40px
		[header-end color-start] auto [color-end clues-start] auto
		[clues-end form-start] 30px;

	grid-column-gap: 0;

	-webkit-column-gap: 0;

	        column-gap: 0;
	grid-row-gap: 10px;
	row-gap: 10px;
}

.game-header {
	grid-column: 1;
	grid-row: 1 / span 2;
	width: 100%;
	height: -webkit-max-content;
	height: -moz-max-content;
	height: max-content;

	display: flex;
	flex-flow: column;
	justify-content: flex-start;
	align-items: flex-start;
}

.game-header h4 {
	margin: 0;
	padding: 1%;
}

.game-header h5 {
	margin: 0;
	padding-bottom: 7px;
	padding-left: 10px;
}

.close-game {
	grid-column: 2;
	grid-row: 1 / span 2;
	width: 40px;
	height: 40px;
	border-radius: 20px;
	padding: 1%;
}

.color-display {
	grid-column: 1 / span 2;
	grid-row: 2;
	width: 100%;
}

/* .message-display {
} */

.messages {
	grid-column: 1 / span 2;
	grid-row: 3;
	width: auto;
	height: 375px;
	background-color: rgba(255, 255, 255, 0.603);
	border-radius: 10px;
	padding: 1%;
	margin-top: 5px;
	margin-bottom: 0px;
	overflow-x: wrap;
	overflow-y: auto;

	display: flex;
	flex-flow: column nowrap;
	align-items: flex-end;
	justify-content: flex-end;
}

.single-message-text {
	font-size: 12pt;
	color: #0c5656;
	padding-left: 5px;
	padding-right: 5px;
	text-align: right;
}

.single-message-name {
	font-size: 10pt;
	color: #2e9d92;
	padding-left: 20px;
	padding-right: 20px;
	padding-bottom: 5px;
	text-align: right;
}

.footer-form {
	grid-column: 1 / span 2;
	grid-row: 4;
	align-self: start;
}

.send-message form {
	width: 100%;
	height: 100%;
}

.textbox {
	position: fixed;
	border-top-left-radius: 5px;
	border-bottom-left-radius: 5px;
	float: left;
	width: 90%;
}

.send-button {
	position: fixed;
	padding: 0;
	margin: 0;
	border-top-right-radius: 5px;
	border-bottom-right-radius: 5px;
	width: 20%;
	height: 20px;
	right: 2.5%;
}

/* ======
DASHBOARD
====== */

.dashboard {
	position: fixed;
	top: 12vh;
	bottom: 80px;
	width: 100%;
	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: center;
	overflow: hidden;
}

.dash-main {
	height: 90%;
	width: 80%;
	padding: 4%;
	border-radius: 10px;
	background-color: turquoise;

	display: flex;
	flex-flow: column nowrap;
	align-items: center;
	justify-content: space-evenly;
}

/* ===
FOOTER
=== */

footer {
	position: fixed;
	left: 0;
	bottom: 0;
	width: 100%;
	height: auto;
	display: flex;
	flex-flow: row wrap;
	align-items: flex-end;
	justify-content: space-evenly;
}

.id-display {
	width: 100%;
	height: 25px;
	margin: 0;
	background-color: darkturquoise;
	display: flex;
	align-items: center;
	justify-content: center;
}

.id-display p {
	font-size: small;
}

.footer-tabs {
	width: 100%;
	height: auto;
	margin: 0;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: space-evenly;
}

button {
	height: 100%;
	width: 100%;
	background-color: turquoise;
	border: none;
}
button:hover {
	background-color: darkcyan;
}
button:active {
	background-color: teal;
	border-top: 2px solid turquoise;
}
